<template>
  <div>
    <van-nav-bar fixed left-arrow :left-text="geoLocationCity" @click-left="$router.back()">
          <van-field v-model="searchKey" placeholder="请输入关键字" slot="title" ref="search" />
    </van-nav-bar>

    <van-index-bar class="indexBarList" :sticky-offset-top="50">
      <template v-for="(cityIndexObj, index) in cityData">
        <van-index-anchor :index="index" />
        <template v-for="city in cityIndexObj">
          <van-cell :title="city.name" clickable @click="select(city)" />
        </template>
      </template>
    </van-index-bar>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        searchKey:''
      }
    },
    computed: {
      cityData() {
        return this.$store.cityData.cities
      },
      geoLocationCity(){
        return this.$store.geoLocationCity
      }
    },
    methods:{
      select(city){
        this.$store.geoLocationCity = city.name
        localStorage.geoLocationCity = city.name
        this.$router.back()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .van-nav-bar{
    z-index: 9999!important;
  }
  .indexBarList {
    margin-top: 50px;
    height: calc(100vh - 80px);
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    background-color: #F8F8F8;
  }
</style>


